<template>
	<main class="page-wrap">
		<masthead :title="title" :subtitle="subtitle" :bg="bg"></masthead>
		<work :categories="categories"></work>
		<media-gallery :title="title" :videos="videos" :images="images" ></media-gallery>
	</main>
</template>

<script>
import { flatten } from '../utils/flatten';
import { head } from '../utils/head';
import Work from './Work';
import Masthead from './Masthead';
import MediaGallery from './MediaGallery';

export default {
	name: 'DetailPage',
	components: {
		Masthead,
		Work,
		MediaGallery,
	},
	props: {
		slugName: {
			type: String,
			required: true
		}
	},
	computed: {
		workDetail() {
			return this.$works.filter( work => work.slug === this.slugName );
		},
		categories() {
			return flatten( this.workDetail.map( work => work.categories ) );
		},
		title() {
			return head( this.workDetail.map( work => work.title ) );
		},
		subtitle() {
			return head( this.workDetail.map( work => work.subtitle ) );
		},
		videos() {
			return flatten( this.workDetail.map( work => work.videos ) );
		},
		images() {
			return flatten( this.workDetail.map( work => work.images ) );
		},
		bg() {
			return head( this.images );
		}
	}
};
</script>



