<template functional>
	<article :class="props.classes">
		<p>
			Integrated strategist passionate about the intersection of brand storytelling, people, culture and digital innovation.
		</p>
	</article>
</template>

<script>
export default {
	name: 'MainCopy',
	props: {
		classes: {
			type: String,
			required: false,
			default: ''
		}
	}
};
</script>

<style scoped>
	p {
		font-size: calc(3vw * var(--font-scaling-ratio));
		line-height: 1.7;
	}
</style>



