import FontFaceObserver from 'fontfaceobserver';

import { globalConfig } from '../global-config';

/**
 * @module FontLoader
 * Utility to watch fonts loaded and notify the app once everything is done
 * by adding a class to root html element
 */

/**
 * @const {Element} root Root `html` node
 */
const root = document.documentElement;

/**
 * @const {array} fonts Collection of fonts as defined in the backend
 */
const { fonts } = globalConfig;

export const FontLoader = {

	fonts,

	updateFonts( fonts ) {

		this.fonts.push( ...fonts );

		return this.fonts;

	},

	updateUI() {

		root.classList.add( 'fonts-loaded' );

	},

	catchError( ...args ) {

		console.log( 'font loading error', ...args );

	},

	init( fonts = [] ) {

		const fontObservers = this.updateFonts( fonts ).map( font => {

			const { family, weight } = font;

			return new FontFaceObserver( family, {
				weight
			} );

		} );

		Promise
			.all( fontObservers.map( font => font.load() ) )
			.then( this.updateUI.bind( this ) )
			.catch( this.catchError );

	}
};



