<template>
	<footer>
		Pixels glued together by <a href="http://www.paolocavanna.it">paolo</a>
	</footer>
</template>

<script>
export default {
	name: 'AppFooter',
};
</script>

<style scoped>
	@keyframes footerBorder {
		from {
			transform: scale3d(0, 0, 0);
		}
		to {
			transform: none;
		}
	}
	
	@media screen and (min-width: 72em) {
		footer {
			position: fixed;
			bottom: 0;
			left: 0;
			transform: rotate(-90deg);
			transform-origin: left -10px;
		}
		
		footer::after {
			height: 2px;
			bottom: -12px;
			left: 0;
			animation: footerBorder 0.5s forwards var(--ease-in-out-circle);
		}
	}
	
	@media screen and (max-width: 72em) {
		footer {
			text-align: right;
			font-size: 0.75rem;
			position: relative;
			margin: auto calc(var(--container-padding-horizontal) / 2 ) var(--container-padding-vertical) auto;
		}
		
		footer::after {
			height: 1px;
			top: -6px;
			right: 0;
			transform: scaleX(0.35);
			transform-origin: right;
		}
	}
	
	footer {
		color: var(--color__dark);
	}
	
	footer::after {
		content: '';
		width: 100%;
		background-color: var(--color__dark);
		position: absolute;
	}
	
	a {
		border: none;
	}
</style>



