<template>
	<figure class="video-wrapper" :style="style">
		<iframe :src="src"
			:title="title"
			webkitallowfullscreen 
			mozallowfullscreen 
			allowfullscreen
			frameborder="0"></iframe>
	</figure>
</template>

<script>
	
export default {
	name: 'VideoWrapper',
	props: {
		src: {
			type: String,
			required: true,
			default: ''
		},
		title: {
			type: String,
			required: false,
			default: ''
		},
		ratio: {
			type: String,
			required: false,
			default: '16-9'
		}
	},
	computed: {
		style() {
				
			const [ width, height ] = this.ratio.split( '-' );
				
			const perc = ( height / width ) * 100;
				
			return {
				'padding-bottom': `${perc}%`
			};
				
		}
	}
};
</script>

<style scoped>
	
	iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
	
	.video-wrapper {
		position: relative;
		height: 0;
	}
	
	@media screen and ( max-width: 72em ) {
		.video-wrapper {
			margin-bottom: 2rem;
		}
	}

</style>



