<template functional>
	<article class="work">
		<section v-for="(category, index) in props.categories" :key="index" class="work__detail">
			<h3 class="work__subtitle">{{category.subtitle}}</h3>
			<p v-if="category.descriptions && category.descriptions.length"
				v-for="(desc, i) in category.descriptions"
				:key="i">{{desc}}</p>
			<ul v-if="category.lists && category.lists.length">
				<li v-for="(list, y) in category.lists" :key="y">{{list}}</li>
			</ul>
		</section>
		
	</article>
</template>

<script>
export default {
	name: 'Work',
	props: {
		categories: {
			type: Array,
			required: true
		}
	},
};
</script>

<style scoped>
	
	p {
		margin-bottom: 0;
	}
	
	article {
		margin-top: 2em;
		margin-bottom: 2em;
	}
	
	.work__detail {
		border: 1px solid var(--color__dark-alpha);
		padding: 0.75em 1.25em;
	}
	
	.work__subtitle {
		background-color: var(--color__light);
		padding: 0 0.5em;
		transform: translateY(-1.5em);
		margin: 0;
		display: inline-block;
	}
	
	.work__subtitle + p {
		transform: translateY(-1.5em);
	}
	
	ul {
		margin: 0;
		list-style: none;
		padding-left: 0;
	}
	
	li {
		position: relative;
		padding-left: 1em;
	}
	
	li:not(:first-child) {
		margin-top: 0.75em;
	}
	
	li::before {
		content: '-';
		transform: scaleX(3) rotate(20deg);
		position: absolute;
		left: 0;
		top: 0;
	}
	
	@media screen and ( max-width: 64em ) {
		.work__detail:not(:first-child) {
			margin-top: 2em;
		}
	}
	
	@media screen and ( min-width: 64em ) {
		h1 {
			font-size: 3vw;
		}
		
		article {
			display: flex;
			background: linear-gradient(135deg, rgba(230, 230, 230, 0.2) 50%, var(--color__light-alpha-norm) 50%) no-repeat 0 -45%;
			background-size: 110% 110%;
			padding: 2em 0 0.5em 0.5em;
		}
		
		.work__detail {
			flex: 1;
		}
		
		.work__detail:not(:first-child) {
			margin-left: 1em;
		}
	}
</style>



