<template functional>
	<header class="box-wrapper">
		<h1 id="main-heading" class="box box--header">
			<a class="box__item" href="https://www.linkedin.com/in/alessandro-mese-bb66026">
				<span id="masthead__name">Alessandro</span>
				<span id="masthead__surname">Mese</span>
			</a>
			<span class="box__item u-text--center">
				/
			</span>
			<span class="box__item box__item--right--large">
				Strategist
			</span>
		</h1>
	</header>
</template>

<script>
export default {
	name: 'Tagline'
};
</script>

<style scoped>
	@keyframes heading {
		0% {
			background-size: 0 3px,
			3px 0,
			0 3px,
			3px 0;
		}
		25% {
			background-size: 100% 3px,
			3px 0,
			0 3px,
			3px 0;
		}
		50% {
			background-size: 100% 3px,
			3px 100%,
			0 3px,
			3px 0;
		}
		75% {
			background-size: 100% 3px,
			3px 100%,
			100% 3px,
			3px 0;
		}
		100% {
			background-size: 100% 3px,
			3px 100%,
			100% 3px,
			3px 100%;
		}
	}
	
	h1 {
		color: var(--color__dark);
		background-repeat: no-repeat;
		background-image: linear-gradient(to right, var(--color__dark) 100%, var(--color__dark) 100%),
			linear-gradient(to bottom, var(--color__dark) 100%, var(--color__dark) 100%),
			linear-gradient(to right, var(--color__dark) 100%, var(--color__dark) 100%),
			linear-gradient(to bottom, var(--color__dark) 100%, var(--color__dark) 100%);
		background-size: 100% 3px,
			3px 100%,
			100% 3px,
			3px 100%;
			background-position: 0 0,
			100% 0,
			100% 100%,
			0 100%;
		animation: heading 1.25s cubic-bezier(0.19, 1, 0.22, 1) 1;
		/*animation-play-state: paused;*/
		margin: auto;
		padding: 0.2em 0.3em;
		position: relative;
		z-index: 2;
		justify-content: space-between;
	}
	
	h1::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		height: 99%;
		width: 100%;
		background-color: var(--color__dark-alpha);
		transform: translate(-0.1em, 0.1em);
	}
	
	h1 > a::after {
		display: none;
	}
	
	.box {
		display: flex;
	}
	
	@media screen and (min-width: 64em) {
		.box__item--right--large {
			text-align: right;
		}
	}
	
	@media screen and (max-width: 64.00625em) {
		.box {
			text-align: center;
			flex-direction: column;
		}
	}

</style>



