const d = document;

const root = d.documentElement;

export const Detection = {
	touch() {

		const touchClass = 'touchstart' in d ? 'touchevents' : 'no-touchevents';

		root.classList.add( touchClass );

		return this;
	},

	init() {

		this.touch();

	}
};



