<template>
	<section class="media-gallery">
		<h2 class="media-gallery__title u-text--center">Media gallery</h2>
		<div class="media-gallery__content media-gallery__content--video">
			<div class="media-gallery__el" v-for="(video, index) in videos" :key="index">
				<video-wrapper
					class="media-gallery__video"
					:src="video"
					:title="title"></video-wrapper>
			</div>
		</div>
		<div class="media-gallery__content">
			<div class="media-gallery__el" v-for="(image, i) in images" :key="'a' + i">
				<lazy-image class="media-gallery__img"
					:src="image"
					:alt="title"></lazy-image>
			</div>
		</div>
	</section>
</template>

<script>
import VideoWrapper from './VideoWrapper';
import LazyImage from './LazyImage';

export default {
	name: 'MediaGallery',
	props: {
		title: {
			type: String,
			required: false,
			default: ''
		},
		videos: {
			type: Array,
			required: false,
			default: () => []
		},
		images: {
			type: Array,
			required: false,
			default: () => []
		}
	},
	components: {
		VideoWrapper,
		LazyImage
	}
};
</script>

<style>
	.media-gallery {
		background: linear-gradient(-5deg, rgba(41, 140, 160, 0.1) 55%, rgba(255, 255, 255, 0.5) 0%);
		background-size: 100% 90%;
		position: relative;
		width: 100vw;
		left: 50%;
		right: 50%;
		margin-left: -50vw;
		margin-right: -50vw;
		padding: var(--container-padding);
	}
	
	@media screen and (min-width: 64em ){
		.media-gallery__content {
			display: flex;
			align-items: center;
			margin-right: -10px;
			margin-left: -10px;
		}
		
		.media-gallery__el {
			flex: 1;
			padding: 0 10px;
			max-width: 75%;
		}
		
		.media-gallery__el:only-of-type {
			margin-right: auto;
			margin-left: auto;
		}
	}
	
	.media-gallery__el {
		margin-bottom: 2em;
	}
	
	.media-gallery__img {
		width: 100%;
		display: block;
	}
	
</style>



