<template>
	<img :src="src"
		:alt="alt"
		class="lazy-img"
		@load="onLoad">
</template>

<script>
/**
 * Create a component to mimic lazy loading of images.
 * @todo:
 *      1 - Pass in non-required options to be merged with required ones
 *      2 - Set min-height
 *      3 - do proper lazy loading by implementing a strategy such as using IntersectionObserver
 *      4 - provide error handling
 *      5 - provide placeholder
 *      6 - provide fallback image(s)
 */
export default {
	name: 'LazyImage',
	props: {
		src: {
			type: String,
			required: true,
		},
		alt: {
			type: String,
			required: true,
		}
	},
	methods: {
		onLoad( { currentTarget } ) {

			currentTarget.classList.add( 'is-loaded' );

		}
	}
};
</script>

<style>
	.lazy-img {
		transition: opacity 0.3s ease-out;
		opacity: 0;
	}
	
	.lazy-img.is-loaded {
		opacity: 1;
	}
</style>



