<template>
	<nav ref="menuList" :class="[isOpen ? 'is-open' : '']">
		<router-link to="/lol-travel">
			<span>lol.travel</span>
		</router-link>
		<router-link to="/peroni">
			<span>Peroni</span>
		</router-link>
		<router-link to="/tempo">
			<span>Tempo</span>
		</router-link>
		<router-link to="/bayer-aspirin">
			<span>Bayer - Aspirin C</span>
		</router-link>
		<router-link to="/bayer-lasonil">
			<span>Bayer - Lasonil</span>
		</router-link>
		<router-link to="/yamaha">
			<span>Yamaha</span>
		</router-link>
	</nav>
</template>

<script>
import { EventBus } from '../utils/event-bus';
import { EVENTS } from '../utils/events';
import { TweenMax, Back } from 'gsap';

const { TOGGLE_MENU } = EVENTS;

export default {
	name: 'MenuList',
	data() {
		return {
			isOpen: false,
		};
	},
	mounted() {
		const { children } = this.$refs.menuList;
		
		const startProps = {
			autoAlpha: 0,
			x: 100,
			y: -100
		};

		TweenMax.set( children, startProps );
		
		EventBus.$on( TOGGLE_MENU, payload => {
			
			this.isOpen = payload;
			
			const props = this.isOpen ? { autoAlpha: 1, y: 0, x: 0, ease: Back.easeOut.config( 1.5 ) } : startProps;
			
			const timing = this.isOpen ? 0.5 : 0.25;

			TweenMax.staggerTo( children, timing, props, 0.15 );
		} );
	}
};
</script>

<style scoped>
	nav {
		background-color: var(--color__complementary);
		padding: 1em 3em 2em;
		position: fixed;
		right: 0;
		top: 39px;
		z-index: 2;
		clip-path: polygon(0 3px, 100% 0, 100% 100%, 0 95%);
		transition-property: opacity, transform;
		transition-duration: 0.35s, 0.5s;
		transition-timing-function: var(--ease-in-out-circle);
		opacity: 0;
		transform: translate3d(100%, 0%, 0);
		box-shadow: inset 0 5px 10px 1px rgba(0, 0, 0, 0.3);
	}
	
	nav.is-open {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}
	
	a {
		display: block;
		text-align: center;
		font-size: calc(3vw * var(--font-scaling-ratio));
		color: var(--color__light);
		opacity: 0.8;
	}
	
	a::after {
		display: none;
	}
	
	span {
		position: relative;
		display: inline-block;
	}
	
	span::after {
		content: '';
		position: absolute;
		left: 0;
		bottom: 6px;
		width: 100%;
		height: 1px;
		background-color: var(--color__light-alpha-norm);
		transform: scaleX(0);
		transition: transform 0.35s var(--ease-in-out-circle);
		transform-origin: bottom right;
	}
	
	.no-touchevents span:hover::after {
		transform: scaleX(1);
		transform-origin: bottom left;
	}
	
	a:not(:first-child) {
		margin-top: 0.75em;
	}
	
	a:not(:last-child) {
		margin-bottom: 0.75em;
	}
	
	.no-touchevents a:hover::after {
		display: none;
	}
</style>



