import Vue from 'vue';
import Router from 'vue-router';

import upperfirst from 'lodash.upperfirst';
import camelcase from 'lodash.camelcase';

import Home from '@/components/Home';

import { works } from '../data';

const _toPascalCase = str => upperfirst( camelcase( str ) );

const _createPath = path => `/${path}`;

const _createDynamicRoutes = () => works.reduce( ( curr, { slug } ) => {

	const loadComponent = () => require( `@/components/${_toPascalCase( slug )}.vue` ).default;

	const component = loadComponent();

	curr.push( {
		path: _createPath( slug ),
		name: slug,
		component
	} );

	return curr;

}, [] );

Vue.use( Router );

const dynamicRoutes = _createDynamicRoutes();

const routes = [
	{
		path: '/',
		name: 'Home',
		component: Home
	},
	...dynamicRoutes
];

export default new Router( {
	mode: 'history',
	routes
} );



