<template>
	<header class="main-header">
		<transition v-if="canShowTitle">
			<h1>
				<router-link to="/">Alessandro Mese</router-link>
			</h1>
		</transition>
		<button ref="menuButton" @click.prevent="toggleMenu" :class="[isOpen ? 'is-open' : '']">
			<menu-icon ></menu-icon>
		</button>
	</header>
</template>

<script>
import { TimelineLite } from 'gsap';
import { EventBus } from '../utils/event-bus';
import { EVENTS } from '../utils/events';

import MenuIcon from './MenuIcon';

const { TOGGLE_MENU } = EVENTS;

const transitionTiming = 0.25;
const rectRotation = 45;
const rectTranslationY = 20;
const rectOrigin = 'center center';

export default {
	name: 'AppHeader',
	components: {
		MenuIcon,
	},
	data(){
		return {
			isOpen: false,
			$svg: null,
			$rect1: null,
			$rect2: null,
			$rect3: null,
		};
	},
	mounted(){
		this.$svg = this.$refs.menuButton.querySelector( '#menu-icon' );
		this.$rect1 = this.$refs.menuButton.querySelector( '#left' );
		this.$rect2 = this.$refs.menuButton.querySelector( '#center' );
		this.$rect3 = this.$refs.menuButton.querySelector( '#right' );
		
		this.setupTimeline();

	},
	methods: {
		setupTimeline(){

			const tl = new TimelineLite( {
				paused: true
			} );
			const $svg = this.$svg;
			const $rect1 = this.$rect1;
			const $rect2 = this.$rect2;
			const $rect3 = this.$rect3;

			tl.timeScale( this.isOpen ? 1.25 : 1 );
			
			tl.to( $svg, transitionTiming, {
				  rotation: 90,
			  } )
			  .to( $rect1, transitionTiming, {
				  y: rectTranslationY
			  } )
			  .to( $rect3, transitionTiming, {
				  y: - rectTranslationY
			  }, `-=${transitionTiming}` )
			  .to( $rect2, transitionTiming, {
				  opacity: 0,
			  }, `-=${transitionTiming}` )
			  .to( $rect1, transitionTiming, {
				  rotation: `${rectRotation}deg`,
				  transformOrigin: rectOrigin
			  } )
			  .to( $rect3, transitionTiming, {
				  rotation: `-${rectRotation}deg`,
				  transformOrigin: rectOrigin,
			  }, `-=${transitionTiming}` )
			  .eventCallback( 'onComplete', () => {
				  this.isOpen = true;
				  EventBus.$emit( TOGGLE_MENU, true );
				  console.log( 'onComplete', this.isOpen );
			  } )
			  .eventCallback( 'onReverseComplete', () => {
				  this.isOpen = false;
				  EventBus.$emit( TOGGLE_MENU, false );
				  console.log( 'onReverseComplete', this.isOpen );
			  } );

			this.$refs.menuButton.animation = tl;
		},
		toggleMenu() {

			this.$refs.menuButton.animation[this.isOpen ? 'reverse' : 'play' ]();
		
		},
	},
	computed: {
		canShowTitle(){
			return this.$route.name !== 'Home';
		}
	},
	watch: {
		$route() {
			if ( this.isOpen ) {
				this.$refs.menuButton.animation.reverse();
			}
		}
	}
};
</script>

<style scoped>
	.main-header {
		text-align: right;
		background-color: var(--color__complementary);
		padding-top: 0.5rem;
		padding-right: 2vmax;
		padding-left: 5vmax;
		display: flex;
		justify-content: space-between;
		min-height: 46px;
		position: sticky;
		top: 0;
		z-index: 2;
	}
	
	.main-header > h1 {
		font-size: calc(3vw * var(--font-scaling-ratio));
		color: #FFF;
	}
	
	.main-header > button {
		margin-left: auto;
	}
</style>



