<template>
	<header class="section-masthead">
		<figure class="section-masthead__bg">
			<lazy-image :src="bg" :alt="title"></lazy-image>
		</figure>
		<section class="section-masthead__text">
			<h1>{{title}}</h1>
			<p class="subtitle">{{subtitle}}</p>
		</section>
	</header>
</template>

<script>
import LazyImage from './LazyImage';

export default {
	name: 'Masthead',
	components: {
		LazyImage,
	},
	props: {
		title: {
			type: String,
			required: true
		},
		subtitle: {
			type: String,
			required: true
		},
		bg: {
			type: String,
			required: false,
			default: '',
		}
		
	}
};
</script>

<style scoped>
	
	.section-masthead {
		height: 100vh;
		display: flex;
		align-items: center;
		position: relative;
		background: no-repeat center;
		background-size: cover;
	}
	
	.section-masthead__text {
		padding: 1em 1.5em;
		background-color: var(--color__dark-alpha);
		transform: translateX(-10%);
	}
	
	.section-masthead__bg {
		position: absolute;
		top: var(--negative-container-padding-horizontal);
		bottom: 0;
		overflow: hidden;
		max-width: none;
		width: 100vw;
		left: 50%;
		right: 50%;
		margin-left: -50vw;
		margin-right: -50vw;
	}
	
	.section-masthead__bg > img {
		filter: saturate(30%) blur(2px);
		opacity: 0.3;
		height: 100%;
		display: block;
		width: 100%;
		object-fit: cover;
		object-position: center;
	}
	
	.section-masthead::before {
		content: '';
		width: 10%;
		height: 101%;
		background-color: var(--color__complementary);
		opacity: 0.5;
		mix-blend-mode: multiply;
		transform: rotate(0.5deg);
	}
	
	@media screen and ( max-width: 64em ) {
		.section-masthead {
			margin-right: -3vw;
			margin-left: -3vw;
		}
	}

</style>



