<template>
	<main class="page-wrap">
		<tag-line></tag-line>
		<main-copy classes="box box--main-copy"></main-copy>
	</main>
</template>

<script>
import MainCopy from './MainCopy';
import TagLine from './TagLine';

export default {
	name: 'Home',
	components: {
		MainCopy,
		TagLine,
	}
};
</script>

<style scoped>
	@media screen and (min-width: 72em) {
		.page-wrap {
			max-width: 60%;
			margin: auto;
		}
	}
</style>



